import axios from 'axios';


class DataService {

    // serverUrl = "http://127.0.0.1:5000";
    serverUrl = "https://petpair.herokuapp.com";

    getPets = async () => {
        let response = await axios.get(`${this.serverUrl}/api/pets`);
        return response.data;
    }

    savePet = async (pet) => {
        let response = await axios.post(`${this.serverUrl}/api/pets`, pet);
        return response.data;
    }

    getComments = async () => {
        let response = await axios.get(`${this.serverUrl}/api/comments`);
        return response.data;
    }

    saveComment = async (comment) => {
        let response = await axios.post(`${this.serverUrl}/api/comments`, comment);
        return response.data;
    }
}

export default DataService;