import React from "react";
import axios from "axios";
import DisplayAnimalCard from "./section/DisplayAnimalCard";
import SearchForm from "./form/SearchForm";
import "../assets/styles/page/browse-and-search.css"
import DataService from "../services/dataService";

export default class BrowseAndSearch extends React.Component {
    state = {
        allPets: [],
        allComments: [],
        searchResults: this.props.animals,
        searchInput: "",
        searchGender: [],
        searchSpecies: [],
        searchStatusTags: [],
        searchFosterAdopt: [],
        searchAgeGte: "",
        searchAgeLte: "",
        commentName: "",
        commentContent: "",
        commentRating: '3',
        commentValid: false
    }

    async componentDidMount() {
        let service = new DataService();
        let allPets = await service.getPets();
        let allComments = await service.getComments();
        console.log(allPets);
        this.setState({
            searchResults: allPets,
            allPets: allPets,
            allComments: allComments
        });
    }

    // function for comment form fields 2 way binding
    updateCommentFormField = (evt) => {
        this.setState({
            [evt.target.name]: evt.target.value,
            commentValid: false
        })
    }

    // function for search form fields 2 way binding
    updateSearchFormField = async (evt) => {
        this.setState({
            [evt.target.name]: evt.target.value
        })
        await setTimeout(() => { this.getSearchResults() }, 1000)
    }

    // function for search checkboxes 2 way binding
    updateSearchCheckbox = async (evt) => {
        let key = evt.target.name
        if (this.state[key].includes(evt.target.value)) {
            let indexToRemove = this.state[key].findIndex((t) => t === evt.target.value)
            this.setState({
                [key]: [...this.state[key].slice(0, indexToRemove), ...this.state[key].slice(indexToRemove + 1)]
            })
        } else {
            this.setState({
                [key]: [...this.state[key], evt.target.value]
            })
        }
        await setTimeout(() => { this.getSearchResults() }, 500)
    }

    // function to get search results
    getSearchResults = async () => {
        // let response = await axios.get(this.props.ANIMALS_API_URL + '/search', {
        //     params: {
        //         searchterm: this.state.searchInput,
        //         gender: this.state.searchGender,
        //         gteyear: this.state.searchAgeGte,
        //         lteyear: this.state.searchAgeLte,
        //         species_name: this.state.searchSpecies,
        //         status_tags: this.state.searchStatusTags,
        //         adopt_foster: this.state.searchFosterAdopt
        //     }
        // })
        // this.setState({
        //     searchResults: response.data
        // })
    }

    commentFormIsValid = () => {
        this.setState({
            commentValid: true
        })
    }

    // function to post new comments
    postComment = async (animal_id, commentDetails) => {
        this.setState({
            commentName: "",
            commentContent: "",
            commentRating: '3',
        })
        // await axios.post(this.props.COMMENTS_API_URL + "/" + animal_id, commentDetails)
        // call the service and the service should post the commentDetails to the server
        let service = new DataService();
        await service.saveComment(commentDetails);
        this.props.processAddDeleteComment()
    }

    // function to delete comments
    deleteComment = async (commentId) => {
        //await axios.delete(this.props.COMMENTS_API_URL + "/" + commentId)
        this.props.processAddDeleteComment()
    }

    applyFiltersAndGetResults = () => {
        let allPets = this.state.allPets;
        // apply search filters
        if (this.state.searchInput !== "") {
            allPets = allPets.filter((pet) => {
                return pet.name.toLowerCase().includes(this.state.searchInput.toLowerCase())
            })
        }
        console.log(this.state.searchGender);
        console.log(this.state.searchSpecies);
        console.log(this.state.searchStatusTags);
        console.log(this.state.searchFosterAdopt);
        console.log(this.state.searchAgeGte);
        console.log(this.state.searchAgeLte);
        console.log(allPets)
        if (this.state.searchGender.length !== 0) {
            allPets = allPets.filter((pet) => {
                return this.state.searchGender.includes(pet.gender)
            })
        }

        if (this.state.searchSpecies.length !== 0) {
            allPets = allPets.filter((pet) => {
                return this.state.searchSpecies.includes(pet.species.species_name)
            });
        }

        if (this.state.searchStatusTags.length !== 0) {
            allPets = allPets.filter((pet) => {

                return pet.status_tags.includes(this.state.searchStatusTags[0]);
            });
        }

        if (this.state.searchFosterAdopt.length !== 0) {
            allPets = allPets.filter((pet) => {
                return pet.adopt_foster.includes(this.state.searchFosterAdopt[0])
            });
        }

        if (this.state.searchAgeGte !== "") {
            allPets = allPets.filter((pet) => {
                let year = pet.date_of_birth.split("-")[0];
                return pet.year <= this.state.searchAgeGte
            });
        }

        if (this.state.searchAgeLte !== "") {
            allPets = allPets.filter((pet) => {
                let year = pet.date_of_birth.split("-")[0];
                return year <= this.state.searchAgeLte
            });
        }

        return allPets;
    };

    renderResults = () => {
        if (this.state.searchResults.length !== 0) {
            return (
                <div className="d-flex flex-wrap justify-content-around">
                    {this.applyFiltersAndGetResults().map((animal, i) => {
                        return (
                            <React.Fragment key={i}>
                                <DisplayAnimalCard i={i}
                                    animal={animal}
                                    comments={this.state.allComments}
                                    setActive={this.props.setActive}
                                    postComment={this.postComment}
                                    deleteComment={this.deleteComment}
                                    updateCommentFormField={this.updateCommentFormField}
                                    commentName={this.state.commentName}
                                    commentContent={this.state.commentContent}
                                    commentRating={this.state.commentRating}
                                    commentValid={this.state.commentValid}
                                    commentFormIsValid={this.commentFormIsValid} />
                            </React.Fragment>)
                    })}
                </div>
            )
        }
    }

    render() {
        return (
            <div className="container-fluid content-container">
                <div className="browse-content row">
                    <h3 style={{ marginBottom: '1rem' }}>Browse all available pets here</h3>
                    <div className="col-lg-3 col-xxl-2">
                        <SearchForm updateSearchFormField={this.updateSearchFormField}
                            updateSearchCheckbox={this.updateSearchCheckbox}
                            searchInput={this.state.searchInput}
                            searchGender={this.state.searchGender}
                            searchSpecies={this.state.searchSpecies}
                            searchStatusTags={this.state.searchStatusTags}
                            searchFosterAdopt={this.state.searchFosterAdopt}
                            searchAgeGte={this.state.searchAgeGte}
                            searchAgeLte={this.state.searchAgeLte}
                            searchResults={this.state.searchResults} />
                    </div>
                    <div className="col-lg-9 col-xxl-10 search-result-container">
                        {this.renderResults()}
                    </div>
                </div>
            </div>

        )
    }
}